n del servicio al usuario. Este servicio es una agenda en la que se inspecciona cuáles documentos no han sido tramitados aún por la oficina productora y, de esta manera, brindar el informe al representante legal o quien haga las veces de principal autoridad. ")]),_c('p',{staticClass:"ms-5"},[_vm._v("En las empresas que realizan procesos de control de manera manual y que la gran mayoría de documentos que se reciben son en papel, basta con observar las planillas de control, realizando un descargue de las respuestas tramitadas, es decir, se realiza un cruce de información con cada unidad administrativa de radicados de entrada con radicados de salida y es así como se obtiene la información de las comunicaciones a las que aún no se les ha dado respuesta. Esta actividad es realmente efectiva, ya que puede ser que el funcionario competente traspapeló dicha comunicación, pero, en el cumplimiento de las funciones, el responsable de la unidad de correspondencia le recuerda la documentación que tiene pendiente por trámite.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-4 ms-5"},[_c('div',{staticClass:"col-xxl-8 col-lg-8 col-md-12 col-sm-12 col-12",attrs:{"data-aos":"fade-down-left"}},[_c('p',[_vm._v("El control automatizado para generar el servicio de alerta se realiza mediante las herramientas que ofrece el sistema de gestión documental o "),_c('i',[_vm._v("software")]),_vm._v(" que maneja la entidad para la administración documental. El representante legal o principal autoridad podrá revisar, en el momento que lo desee, a través de la descarga del informe sobre los avances en el trámite de correspondencia, y determinará las unidades administrativas con bandejas de entrada en cero y aquellas cuyas solicitudes ya se encuentran tardías por respuesta; este es un control veraz hacia la eficacia de la empresa. En muchas empresas, se realizan planes de contingencia par